.recipe-aside {
	width: 100%;
	background-color: #ffeafe;
    margin-bottom: 48px;
    margin-right: 1%;
    margin-left: 1%;
	
	
}



.recipe-aside a {
	text-decoration: none;
}
.date, .title {
	padding-top: 24px;
	padding-left: 10px;
}
.date {
	font-size: 14px;
	color: #553c8b;
}
.title {
	font-size: 24px;
	font-weight: bold;
	padding-top: 14px;
	padding-bottom: 48px;
    color: black;
    padding-right: 10px;
}

.title:hover {
	text-decoration: underline;
}
img {
    width: 100%;
}
