.recipe {
	width: 23%;
	background-color: #ffeafe;
    margin-bottom: 48px;
    margin-right: 1%;
	margin-left: 1%;
	position: relative;
	
	
}



.recipe a {
	text-decoration: none;
}
.date, .title {
	padding-top: 24px;
	padding-left: 10px;
}
.date {
	font-size: 14px;
	color: #553c8b;
}
.title {
	font-size: 24px;
	font-weight: bold;
	padding-top: 14px;
	padding-bottom: 48px;
    color: black;
    padding-right: 10px;
}

.title:hover {
	text-decoration: underline;
}
img {
    width: 100%;
}

button {
	position: absolute;
	right: 10px;
	bottom: 10px;
	background: transparent;
	border-style: none;
	outline: none;
}
.like {
	
	font-size: 30px;
}

@media(max-width: 768px) {
	.recipe {

		width: 47%;
		margin-right: 1.5%;
		margin-left: 1.5%;
		
	}
}
@media (max-width: 575px) {
	.recipe {

		width: 100%;
		margin-bottom: 10px;
		
	}
}